import trollFace from './trollFace.png'
import { RiGithubLine } from './Icons'

let Header = () => {
    return (
        <nav className="nav">
            <div className="nav--left">
                <img src={trollFace} alt="logo" />
                <span>Meme Generator</span>
            </div>
            <div className="nav--right">
                <a target='_blank' href="https://www.github.com/wolfofweb" rel="noreferrer">
                    <RiGithubLine id="githubIcon" /> Github</a>
            </div>
        </nav >
    )
}
export default Header;