import React from 'react';
//External libraries for created meme image download
import html2canvas from 'html2canvas';

let Meme = () => {
    //State for meme object with initial image
    let [meme, setMeme] = React.useState(
        {
            topText: "",
            bottomText: "",
            randomImage: "https://i.imgflip.com/2ji8hx.jpg"
        })
    //handling state changes in top and bottom text dynamically
    let handleChange = (event) => {
        let { name, value } = event.target
        setMeme(prevMeme => {
            return { ...prevMeme, [name]: value }
        })
    }
    //useEffect to fetch API data once during the app's first render
    //As the dependency array is [], this renders the useEffect on page first render only
    //2)Setting API Data to the state allMemes for storing API data into it
    React.useEffect(
        () => {
            fetch("https://api.imgflip.com/get_memes").then(response =>
                response.json()
            ).then(data => setAllMemes(data.data.memes))
        }, []
    )
    //1)State for meme Image, Initial state is empty array
    let [allMemes, setAllMemes] = React.useState([])
    //Get Meme Image function for generating random image
    //This function assigns random image to our Meme, setMeme state....(i,e) our Meme top, bottom, image object state
    function getMemeImage() {
        let randomNumber = Math.floor(Math.random() * allMemes.length)
        let url = allMemes[randomNumber].url
        setMeme((prevMeme) => ({ ...prevMeme, randomImage: url }))
    }

    //Drawing canvas and generating download link for created meme image downloading
    //We have used html2canvas external library
    let printRef = React.useRef();
    let handleDownloadImage = async () => {
        let element = printRef.current;
        let canvas = await html2canvas(element, {
            logging: true, letterRendering: 1, allowTaint: false, useCORS: true
        });
        let data = canvas.toDataURL('image/jpg');
        let link = document.createElement('a');
        link.href = data;
        link.download = 'image.jpg';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    };
    //Our Meme Component DOM
    return (
        <div className="main">
            <div className="form">
                <div className="inputs">
                    <input type="text" value={meme.topText}
                        placeholder="Top text"
                        onChange={handleChange}
                        name="topText" />

                    <input type="text" value={meme.bottomText}
                        placeholder="Bottom text" onChange={handleChange} name="bottomText" />
                </div>
                <div className="submit">
                    <button onClick={getMemeImage}>Get a new meme image</button>
                </div>
            </div>
            {/* Assigning ref(Reference) here to scrap only this html element and download as image */}
            <div className='meme' ref={printRef}>
                <img className='memeImg' alt="" src={meme.randomImage} />
                <h2 className="meme--text top">{meme.topText}</h2>
                <h2 className="meme--text bottom">{meme.bottomText}</h2>
            </div>
            <div className='download'>
                <button onClick={handleDownloadImage} className='download-button'>Download</button>
            </div>
        </div>
    )
}
export default Meme